/**
 * Cascader key  
 *
 * @param {any} arr
 * @param {any} values
 * @param {any} labelName
 * @returns
 */
export const getEnumLabels = (arr, values, labelName) => {
  if (!arr || arr.length === 0) {
    return undefined
  }
  var result = []
  var arrs = arr
  values.forEach(v => {
    const obj = getMatchedValue(arrs, v, labelName)
    result.push(obj.label)
    arrs = obj.children
  })
  return result
}

/**
 * Cascader,
 *
 * @param {any} arr
 * @param {any} obj
 */
export const unboxSingleCascader = (arr, obj) => {
  arr.forEach(key => {
    obj[key] = obj[key][0]
  })
  return obj
}



