import React, { PureComponent } from 'react'
import { Breadcrumb } from 'antd'
import s from 'shared/title.css'
import { navTree } from 'constants/TEXT'

class EnterpriseDetailTitle extends PureComponent {
  render() {
    return (
      <div className={s.title}>
        <Breadcrumb>
          <Breadcrumb.Item>
            <span>{navTree.uc.enterprise.title}</span>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            <span>{navTree.uc.enterprise.enterpriseDetail.title}</span>
          </Breadcrumb.Item>
        </Breadcrumb>
      </div>
    )
  }
}

export default EnterpriseDetailTitle



